import React from "react";
import { useState } from "react";
import { StaticImage } from "gatsby-plugin-image";
import { Container, Row, Col } from "react-bootstrap";
import { Country } from "country-state-city";
import { collection, addDoc } from "firebase/firestore/lite";
import { utils } from "../../core/helper";
import { useForm } from "react-hook-form";
import { yupResolver } from "@hookform/resolvers/yup";
import * as Yup from "yup";
import Loading from "../../components/common/loading";

const emailRegex = /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i;

const validationSchema = Yup.object().shape({
  name: Yup.string().label("Name").required(),
  email: Yup.string()
    .matches(emailRegex, "Invalid email address")
    .label("Email")
    .required(),
  country: Yup.string().label("Country").required(),
  mobile: Yup.string().label("Mobile Number or Skype ID").required(),
  content: Yup.string().label("Content").required(),
});

const ContactForm = (props) => {
  const [loading, setLoading] = useState(false);

  const { db } = props;
  const {
    register,
    handleSubmit,
    formState: { errors },
    reset,
  } = useForm({
    resolver: yupResolver(validationSchema),
  });

  const countries = Country.getAllCountries();

  const handleOnSubmit = async (values) => {
    const { name, email, country, content, mobile } = values;
    const payload = {
      name,
      email,
      country,
      content,
      mobile,
      date: new Date(),
    };
    setLoading(true);
    try {
      // return false
      const clients = collection(db, "clients");
      // const resp = await addDoc(clients, payload);
      utils.showSuccssMsg("We will contact/talk to you soon");
      reset();
      setLoading(false);
    } catch (e) {
      utils.showErrMsg("Error adding document");
      setLoading(false);
    }
  };

  return (
    <section className="justtry_cta_sec">
      <Container>
        <Row style={{ marginTop: "30px", rowGap: "25px" }}>
          <Col
            xxl={6}
            xl={6}
            lg={6}
            md={6}
            className="d-flex align-items-center justify-content-center "
          >
            <div className="have_question_lhs">
              <StaticImage
                width={500}
                height={500}
                src="../../styles/assets/images/recentworks/have_question.png"
                alt="Having any question?"
              />
            </div>
          </Col>
          <Col
            xxl={6}
            xl={6}
            lg={6}
            md={6}
            className="d-flex align-items-center justify-content-center "
          >
            <div className="have_question_rhs">
              <h1>Have questions? Shoot us an email.</h1>
              <div className="have_question_inner_text">
                <p>
                  Our seasoned team of developers, designers, testers, and
                  digital marketers excels in handling intricate projects. We
                  ensure product quality through proven practices, creating
                  prototypes aligned with client needs.
                </p>{" "}
                <p>
                  Client approval is sought at each development stage. Rigorous
                  testing guarantees bug-free, fully functional products
                  surpassing client expectations.
                </p>
              </div>
            </div>
          </Col>
        </Row>
        <div className="d-flex align-items-center justify-content-center">
          <Row className="justtry_cta_form_wrapper">
            <Col
              xxl={7}
              xl={6}
              lg={6}
              md={12}
              className="d-flex align-items-center justify-content-center"
            >
              <div className="justtry_cta_form_sec">
                <h1>
                  Get in <span style={{ color: "#7534fc" }}>Touch</span>
                </h1>
                <p>
                  We're available to assist and address any inquiries you may
                  have. We eagerly anticipate receiving your communication.
                </p>
                <form
                  onSubmit={handleSubmit(handleOnSubmit)}
                  className="justtry_cta_form"
                >
                  <div className="justtry_cta_form_content">
                    <div className="justtry_cta_form_lhs">
                      <input
                        placeholder="Enter your name"
                        id="full-name"
                        type="text"
                        {...register("name")}
                        maxLength={30}
                      />
                      <p
                        style={{
                          fontSize: "12px",
                          color: "#f03e3e",
                          fontFamily: "Poppins",
                          fontStyle: "normal",
                          fontWeight: 400,
                          lineHeight: "20px",
                        }}
                      >
                        {errors.name?.message}
                      </p>
                    </div>
                    <div className="justtry_cta_form_rhs">
                      <input
                        placeholder="Enter your mail"
                        id="email"
                        type="email"
                        {...register("email")}
                        maxLength={50}
                      />
                      <p
                        style={{
                          fontSize: "12px",
                          color: "#FF0000",
                          fontFamily: "Poppins",
                          fontStyle: "normal",
                          fontWeight: 400,
                          lineHeight: "20px",
                        }}
                      >
                        {errors.email?.message}
                      </p>
                    </div>
                  </div>
                  <div className="justtry_cta_form_content">
                    <div className="justtry_cta_form_lhs">
                      <input
                        placeholder="Skype/Mobile/Telegram"
                        id="mobile"
                        type="text"
                        {...register("mobile")}
                        maxLength={50}
                      />
                      <p
                        style={{
                          fontSize: "12px",
                          color: "#FF0000",
                          fontFamily: "Poppins",
                          fontStyle: "normal",
                          fontWeight: 400,
                          lineHeight: "20px",
                        }}
                      >
                        {errors.mobile?.message}
                      </p>
                    </div>
                    <div className="justtry_cta_form_rhs">
                      <select id="select-where" {...register("country")}>
                        <option value="" disabled selected>
                          Select your Country
                        </option>
                        {countries.map((ele, ind) => {
                          return (
                            <option key={ind} value={ele.isoCode}>
                              {ele.name}
                            </option>
                          );
                        })}
                      </select>
                      <p
                        style={{
                          fontSize: "12px",
                          color: "#FF0000",
                          fontFamily: "Poppins",
                          fontStyle: "normal",
                          fontWeight: 400,
                          lineHeight: "20px",
                        }}
                      >
                        {errors.country?.message}
                      </p>
                    </div>
                  </div>
                  <div className="justtry_cta_textarea">
                    <textarea
                      placeholder="Text your message here..."
                      id="content"
                      rows={5}
                      {...register("content")}
                      maxLength={200}
                    />
                    <p
                      style={{
                        fontSize: "12px",
                        color: "#FF0000",
                        fontFamily: "Poppins",
                        fontStyle: "normal",
                        fontWeight: 400,
                        lineHeight: "20px",
                      }}
                    >
                      {errors.content?.message}
                    </p>
                  </div>
                  <div className="justtry_cta_sumbit">
                    <button disabled={loading}>
                      Submit {loading && <Loading />}
                    </button>
                  </div>
                </form>
              </div>
            </Col>
            <Col
              xxl={5}
              xl={6}
              lg={6}
              md={12}
              className="d-flex align-items-center justify-content-center"
            >
              <div className="justtry_cta_map_sec">
                <iframe
                  title="Justtry Office"
                  src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3930.2126470840517!2d78.1340045741623!3d9.916240074472167!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x3b00c539e6118b11%3A0x6c3891e5d1526c93!2sJusttry%20Technologies!5e0!3m2!1sen!2sin!4v1694181975876!5m2!1sen!2sin"
                  className="map_frame"
                  allowFullScreen=""
                  loading="lazy"
                  referrerPolicy="no-referrer-when-downgrade"
                ></iframe>
              </div>
            </Col>
          </Row>
        </div>
        <div className="d-flex align-items-center justify-content-center ">
          <Row className="justtry_contact_section_row">
            <Col
              xxl={4}
              xl={4}
              lg={4}
              md={12}
              className="d-flex justify-content-center  align-items-center"
            >
              <div className="justtry_links_lhs">
                {utils.LoctionIcon()}
                <p>
                  179/3, Kamarajar salai,
                  <br />
                  (Near Tamilnadu Chamber of Commerce),
                  <br />
                  Madurai – 625009.
                </p>
              </div>
            </Col>
            <Col xxl={8} xl={8} lg={8} md={12}>
              <Row style={{ rowGap: "20px" }}>
                <Row style={{ rowGap: "25px" }}>
                  <Col
                    xxl={4}
                    xl={4}
                    lg={4}
                    md={12}
                    className="d-flex justify-content-center  align-items-center"
                  >
                    <a
                      href="https://t.me/justtrytech"
                      target="_blank"
                      rel="noreferrer"
                      aria-label="Reach Justty and build your website"
                    >
                      <div className="justtry_links_box">
                        {utils.TelegramIcon()}
                        <p>Justtry-official</p>
                      </div>
                    </a>
                  </Col>
                  <Col
                    xxl={4}
                    xl={4}
                    lg={4}
                    md={12}
                    className="d-flex justify-content-center  align-items-center"
                  >
                    <a
                      target="_blank"
                      rel="noreferrer"
                      href="https://wa.me/+919500139200"
                      aria-label="Reach Justty and build your website"
                    >
                      <div className="justtry_links_box">
                        {utils.WhatsappIcon()}
                        <p>+91 9500 139 200</p>
                      </div>
                    </a>
                  </Col>
                  <Col
                    xxl={4}
                    xl={4}
                    lg={4}
                    md={12}
                    className="d-flex justify-content-center  align-items-center"
                  >
                    <a
                      target="_blank"
                      rel="noreferrer"
                      href="skype:live:.cid.74dc6fc29052d565?chat"
                      aria-label="Reach Justty and build your website"
                    >
                      <div className="justtry_links_box">
                        {utils.SkypeIcon()}
                        <p>@justtrytech</p>
                      </div>
                    </a>
                  </Col>
                </Row>
                <Row style={{ rowGap: "25px" }}>
                  <Col
                    xxl={6}
                    xl={6}
                    lg={5}
                    md={12}
                    className="d-flex justify-content-center  align-items-center"
                  >
                    <a
                      target="_blank"
                      href="https://www.linkedin.com/company/justtry-technologies/?originalSubdomain=in9"
                      rel="noreferrer"
                      aria-label="Reach Justty and build your website"
                    >
                      <div className="justtry_links_big_box">
                        {utils.LinkedinIcon()}
                        <p>company/justtry-technologies</p>
                      </div>
                    </a>
                  </Col>
                  <Col
                    xxl={6}
                    xl={6}
                    lg={6}
                    md={12}
                    className="d-flex justify-content-center  align-items-center"
                  >
                    <a
                      target="_blank"
                      rel="noreferrer"
                      href="mailto:justtrytechnologies@gmail.com"
                      aria-label="Reach Justty and build your website"
                    >
                      <div className="justtry_links_big_box">
                        {utils.GmailIcon()}
                        <p>justtrytechnologies@gmail.com</p>
                      </div>
                    </a>
                  </Col>
                </Row>
                {/* <div className="justtry_links_rhs">
          <div className="justtry_links_row">
            <div className="justtry_links_box">
              {utils.TelegramIcon()}
              <p>Justtry-official</p>
            </div>
            <div className="justtry_links_box">
              {utils.WhatsappIcon()}
              <p>+91 9500 139 200</p>
            </div>
            <div className="justtry_links_box">
              {utils.SkypeIcon()}
              <p>@justtrytech</p>
            </div>
          </div>
          <div className="justtry_links_row">
            <div className="justtry_links_big_box">
              {utils.LinkedinIcon()}
              <p>company/justtry-technologies</p>
            </div>
            <div className="justtry_links_big_box">
              {utils.GmailIcon()}
              <p>justtrytechnologies@gmail.com</p>
            </div>
          </div>
        </div> */}
              </Row>
            </Col>
          </Row>
        </div>
      </Container>
    </section>
  );
};

export default ContactForm;
